.material .material-input {
    position: relative;
    padding-top: 18px;
    margin-bottom: 10px;
    background: #fff
}

.material .material-input:after {
    display: block;
    clear: both;
    content: ""
}

.material .material-input input, .material .material-input textarea {
    font-size: 16px;
    width: 100%;
    border: 0;
    border-bottom: 1px solid #d3d3d3;
    outline: 0;
    background: 0;
    float: left
}

.material .material-input textarea {
    height: 120px;
    padding: 7px 0
}

.material .material-input input {
    height: 32px
}

.material .material-input label {
    line-height: 32px;
    color: gray;
    font-size: 16px;
    font-weight: 300;
    position: absolute;
    left: 0;
    top: 18px
}

.material .material-bar {
    position: absolute;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%
}

.material .material-bar:after, .material .material-bar:before {
    content: "";
    height: 2px;
    width: 0;
    bottom: 0;
    position: absolute;
    background: #00f
}

.material .material-bar:before {
    left: 50%
}

.material .material-bar:after {
    right: 50%
}

.material .error ~ .material-bar:after, .material .error ~ .material-bar:before, .material .material-input :focus ~ .material-bar:after, .material .material-input :focus ~ .material-bar:before {
    width: 50%
}

.material .error ~ .material-bar:after, .material .error ~ .material-bar:before {
    background: red
}

.material .error.filled, .material .error:focus, .material .error:not(.filled) ~ label {
    color: red
}

.material .error:focus ~ label, .material .material-input .filled ~ label, .material .material-input :focus ~ label {
    color: #d3d3d3;
    font-size: 13px;
    top: 0;
    line-height: 24px
}

.material .material-select {
    font-size: 16px;
    font-weight: 300;
    width: 100%;
    position: relative;
    margin-bottom: 10px;
    padding-top: 18px
}

.material .material-select input {
    display: none
}

.material .material-select > label {
    color: gray;
    border-bottom: 1px solid #d3d3d3;
    height: 40px;
    line-height: 40px;
    padding: 0 30px 0 0;
    width: 100%;
    cursor: pointer;
    position: relative;
    display: block
}

.material .material-select > .material-title {
    position: absolute;
    color: #d3d3d3;
    height: 40px;
    line-height: 40px;
    top: 18px;
    left: 0;
    opacity: 0;
    visibility: hidden
}

.material .material-select.filled > .material-title, .material .material-select > input:checked + .material-title {
    top: 0;
    opacity: 1;
    visibility: visible;
    font-size: 13px;
    line-height: 24px
}

.material .material-select > label strong {
    border-color: #d3d3d3 transparent transparent;
    border-style: solid;
    border-width: 6px 5px 0;
    margin-top: -3px;
    width: 0;
    height: 0;
    position: absolute;
    right: 0;
    top: 50%
}

.material .material-select > input:checked ~ ul {
    visibility: visible;
    opacity: 1;
    top: 24px
}

.material .material-select ul {
    background: #fff;
    top: 0;
    left: 0;
    width: 100%;
    position: absolute;
    opacity: 0;
    visibility: hidden;
    z-index: 2
}

.material .material-select ul li label {
    color: gray
}

.material .material-select ul li input:checked + label, .material .material-select ul li:hover input:checked + label {
    font-weight: 400
}

.material .material-select ul li label {
    display: block;
    cursor: pointer;
    padding: 7px 10px
}

.material .material-select ul li label:hover {
    background: #f5f5f5
}

.material .material-select > input:checked + .material-bar {
    top: 22px
}

.material .material-select .material-bar {
    height: 2px;
    background: #00f;
    width: 0;
    bottom: auto;
    top: 56px
}

.material .material-select ul {
    box-shadow: 0 2px 2px rgba(0, 0, 0, .2)
}

.material .material-bar:after, .material .material-bar:before, .material .material-input label, .material .material-select .material-bar, .material .material-select ul, .material .material-select > span {
    transition: .2s ease all;
    -moz-transition: .2s ease all;
    -webkit-transition: .2s ease all
}

.material {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none
}

.material *, .material :after, .material :before {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box
}
